import React from 'react';
import logo from './gearfinder.jpg';
import './App.css';



class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: true,
      order_status: "",
      order_id: ""
    };
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(event) {
    
    this.setState({order_id: event.target.value});
  }
  async handleFormSubmit (e) {    
    e.preventDefault();
    let url = 'https://my-app-1.serjshaklein.workers.dev/orders/'+this.state.order_id;
    //let url = 'http://127.0.0.1:8787/orders/'+this.state.order_id;
     await fetch(url, {crossDomain:true, method: 'GET'})
     .then(response => response.text())
    .then(result => {
          this.setState({
          isLoaded: true,
          order_status: result
      })
})
  }

  render() {
    
    const { error, isLoaded,  order_status, order_id } = this.state;
   
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="App">
        <div id="rec266816760" class="r t-rec"  >
       
        <div class="t-cover" id="recorddiv266816760" bgimgfield="img"  >
           <div class="t-cover__carrier" id="coverCarry266816760"  ></div>
           <div class="t-cover__filter t-cover__filter_form" ></div>
           <div class="t720">
              <div class="t-container">
                 <div class="t-width t-width_10 t720__mainblock t-align_center">
                    <div class="t-cover__wrapper t-valign_middle z-index-wrapper" >
                       <div class="t720__mainwrapper" >
                          <div class="t720__textwrapper">
                             <div class="t720__title t-title t-title_sm t-margin_auto"  field="title">Хотите узнать статус заказа?</div>
                             <div class="t720__descr t-descr t-descr_xl t-margin_auto"  field="descr">Введите Order ID</div>
                          </div>
                       </div>
                       <form id="form266816760" name='form266816760' role="form" onSubmit={this.handleFormSubmit}  class="t-form js-form-proccess t-form_inputs-total_1 " >
                          <div class="t-form__inputsbox">
                             <div class="t-input-group t-input-group_em" >
                                <div class="t-input-block">
                                   <input type="text"  class="t-input js-tilda-rule order-id" placeholder="Order ID"  name="order_id" id="order_id" value={this.state.order_id } onChange={this.handleChange}   /> 
                                </div>
                             </div>
                             <div class="t-form__submit"> <button type="submit" class="t-submit t-button"  >Check</button>
                             </div>
                          </div>
                       </form>
                       <p class="order_status">
          {this.state.order_status}
          </p>
                    </div>
                 </div>
              </div>
           </div>
        </div>
     </div>
      </div>
      )
    }
  }
}

export default MyComponent;
